import React, { useContext, useEffect, useState } from "react";
import Context from "../utils/context";
import axios from "axios";
import history from "../utils/history";
import RichTextEditor from "react-rte";
import CareerBtnRenamer from "../components/CareerBtnRenamer";
import CareerBtnSwitcher from "../components/CareerBtnSwitcher";
import CareerBtnAdder from "../components/CareerBtnAdder";
import Step1 from "../components/Step1";
import Step2 from "../components/Step2";
import Step3 from "../components/Step3";
import Step4 from "../components/Step4";
import Step5 from "../components/Step5";
import Step6 from "../components/Step6";
import StepSave from "../components/StepSave";

export default function HomePage() {
  const context = useContext(Context);
  /* the following number will later be initialized as the current 
  career number value given by the db or 0 as in ( dbVal || 0 ) instead of simply 0.*/
  const [currentCareerNum, setCurrentCareerNum] = useState(0);
  /* the following list will later come from the db. If this is the user's 
  first time logging in, it will be initialised as an array with a single item as in ["Default"]*/
  const [careersList, setCareersList] = useState(["Default", "Career 2", "Career 3"]);
  const [jobPostingURL, setJobPostingURL] = useState("");
  const [jobDescription, setJobDescription] = useState(RichTextEditor.createEmptyValue());
  const [companyName, setCompanyName] = useState("");
  const [jobTitle, setJobTitle] = useState("");
  const [jobNotes, setJobNotes] = useState(RichTextEditor.createEmptyValue());
  const [droppedFiles, setDroppedFiles] = useState([]);
  const [tags, setTags] = useState("");

  // Test server connection on render
  // useEffect(() => {
  //   axios
  //     .get("/api/connect")
  //     .then((res) => console.log(res.data))
  //     .catch((err) => console.log(err))
  //     .then(setTimeout(() => history.replace("/"), 700));
  // }, []);

  useEffect(() => {
    // update currentCareerNum in db whenever currentCareerNum changes in the client side
  }, [currentCareerNum]);

  useEffect(() => {
    // setCurrentDisplayCareer(careersList[currentCareerNum]);
    // update careersList in db, and more importantly, all job applications made under the careers that have been changed must now have their career values updated to the new career names
  }, [careersList]);

  function getDateNumber() {
    const timeStamp = new Date();
    let year = timeStamp.getFullYear().toString();
    let month = (timeStamp.getMonth() + 1).toString();
    let day = timeStamp.getDate().toString();
    if (month.length === 1) {
      month = "0" + month;
    }
    if (day.length === 1) {
      day = "0" + day;
    }
    return parseInt(year + month + day);
  }

  function clearForm() {
    setJobPostingURL("");
    setJobDescription(RichTextEditor.createEmptyValue());
    setCompanyName("");
    setJobTitle("");
    setJobNotes(RichTextEditor.createEmptyValue());
    setDroppedFiles([]);
    let nodeList = document.getElementsByTagName("input");
    let nodeListConvertedToArray = Array.prototype.slice.call(nodeList);
    nodeListConvertedToArray.forEach((input) => {
      input.value = "";
    });
    document.querySelector("#step1Heading").style.color = "initial";
    document.querySelector("#step3Heading").style.color = "initial";
  }

  function handleFilesDrop(files) {
    setDroppedFiles([...droppedFiles, ...files]);
  }

  function handleSubmit() {
    if (companyName.length === 0 || jobTitle.length === 0) {
      document.querySelector("#step3Heading").style.color = "red";
      alert("Save unsuccessful. Required data is missing.");
      return;
    }
    const data = {
      jobPostingURL: jobPostingURL,
      jobDescription: jobDescription.toString("html"),
      companyName: companyName,
      jobTitle: jobTitle,
      jobNotes: jobNotes.toString("html"),
      droppedFiles: droppedFiles,
      tags: tags,
      careersList: careersList,
      currentCareerNum: currentCareerNum,
      careerName: careersList[currentCareerNum],
      username: context.isAuthenticated ? context.user.email : "demo",
      applicationDate: getDateNumber(),
    };
    console.log(data);
    axios
      .post("/api/post/posttodb", data)
      .then((res) => {
        console.log(res);
        if (context.isAuthenticated) {
          alert(`Saved to user ${context.user.email}'s database.`);
        } else {
          alert(`Saved to public demonstration database.`);
        }
        console.log(res.data);
        clearForm();
      })
      .catch((err) => {
        console.log(err);
        alert("Website under development");
      });
    // .then(setTimeout(() => history.replace("/search"), 700));
  }

  return (
    <div className="centeredPage">
      <h1>Home</h1>
      <h3>
        Record a new application for career:&nbsp;
        <span className="text-success">{careersList[currentCareerNum]}</span>
      </h3>

      <div className="d-flex">
        <CareerBtnRenamer
          careersList={careersList}
          setCareersList={setCareersList}
          currentCareerNum={currentCareerNum}
        />
        {careersList.length > 1 && (
          <CareerBtnSwitcher
            careersList={careersList}
            currentCareerNum={currentCareerNum}
            setCurrentCareerNum={setCurrentCareerNum}
          />
        )}
        <CareerBtnAdder
          careersList={careersList}
          setCareersList={setCareersList}
          setCurrentCareerNum={setCurrentCareerNum}
        />
      </div>
      <Step1 setJobPostingURL={setJobPostingURL} />
      <Step3 setCompanyName={setCompanyName} setJobTitle={setJobTitle} />
      <div className="d-flex">
        <Step2 jobDescription={jobDescription} setJobDescription={setJobDescription} />
        <Step4 jobNotes={jobNotes} setJobNotes={setJobNotes} />
      </div>
      <Step5
        handleFilesDrop={handleFilesDrop}
        droppedFiles={droppedFiles}
        setDroppedFiles={setDroppedFiles}
      />
      <Step6 setTags={setTags} />
      <StepSave handleSubmit={handleSubmit} />
    </div>
  );
}
