import React, { useReducer, useEffect } from "react";
import Routes from "./Routes";
import Context from "./utils/context";
import * as AuthReducer from "./store/reducers/authReducer";
import * as ACTIONS from "./store/actions/actions";
import { useAuth0 } from "@auth0/auth0-react";
import history from "./utils/history";

function App() {
  const auth0 = useAuth0();

  /*Auth Reducer: although auth0 object keeps global auth state, we'll keep our own  */
  const [stateAuthReducer, dispatchAuthReducer] = useReducer(
    AuthReducer.AuthReducer,
    AuthReducer.initialState
  );

  const dispatchLogin = () => {
    dispatchAuthReducer(ACTIONS.login_success());
  };

  const dispatchLogout = () => {
    dispatchAuthReducer(ACTIONS.login_failure());
  };

  const dispatchAddAuthProfile = (profile) => {
    dispatchAuthReducer(ACTIONS.add_profile(profile));
  };

  const dispatchRemoveAuthProfile = () => {
    dispatchAuthReducer(ACTIONS.remove_profile());
  };

  /* When the auth state changes, dispatch the auth reducer */
  useEffect(() => {
    setTimeout(() => {
      history.replace("/dispatchauth");
    }, 200);
  }, [auth0.isAuthenticated]);

  return (
    <Context.Provider
      value={{
        auth0: auth0,
        user: auth0.user,
        isAuthenticated: auth0.isAuthenticated,
        stateAuthReducer: stateAuthReducer,
        dispatchLogin: () => dispatchLogin(),
        dispatchLogout: () => dispatchLogout(),
        dispatchAddAuthProfile: (profile) => dispatchAddAuthProfile(profile),
        dispatchRemoveAuthProfile: () => dispatchRemoveAuthProfile(),
      }}
    >
      <Routes />
    </Context.Provider>
  );
}

export default App;
