import { useState } from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

export default function CareerBtnRenamer({ careersList, setCareersList, currentCareerNum }) {
  const [renamedCareer, setRenamedCareer] = useState(careersList[currentCareerNum]);
  const [modalOn, setModalOn] = useState(false);

  function toggleModal() {
    setModalOn(!modalOn);
  }

  function rename() {
    if (renamedCareer.length === 0 || renamedCareer === careersList[currentCareerNum]) {
      return;
    }
    /* must not create duplicates */
    if (
      careersList.find((item) => {
        return item === renamedCareer;
      })
    ) {
      alert(`Error: "${renamedCareer}" already exists. Cannot create duplicate careers.`);
      // setRenamedCareer("");
    } else {
      careersList[currentCareerNum] = renamedCareer;
      setCareersList([...careersList]);
      toggleModal();
    }
  }

  return (
    <div>
      <div className="dropdown">
        <button type="button" className="btn btn-outline-success" onClick={toggleModal}>
          Rename Career
        </button>
        <Modal isOpen={modalOn} toggle={toggleModal}>
          <ModalHeader toggle={toggleModal}>Rename "{careersList[currentCareerNum]}":</ModalHeader>
          <ModalBody>
            <input
              name="renameCareer"
              id="renameCareer"
              type="text"
              className="form-control"
              title="Rename Career"
              placeholder="New name"
              onChange={(e) => {
                setRenamedCareer(e.target.value);
              }}
            />
            <button type="button" className="btn btn-success w-100" onClick={rename}>
              Rename
            </button>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={toggleModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    </div>
  );
}
