import React from "react";

export default function Step1({ setJobPostingURL }) {
  return (
    <div className="step w-50">
      <h3 id="step1Heading">Copy and paste the job posting webpage URL.</h3>
      <input
        id="jobPostingURL"
        name="jobPostingURL"
        className="form-control"
        type="url"
        title="Job Posting URL"
        onChange={(e) => {
          setJobPostingURL(e.target.value);
        }}
      />
    </div>
  );
}
