import { useState } from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

export default function CareerBtnAdder({ careersList, setCareersList, setCurrentCareerNum }) {
  const [newCareer, setNewCareer] = useState("");
  const [modalOn, setModalOn] = useState(false);

  function toggleModal() {
    setModalOn(!modalOn);
  }

  function addCareer() {
    if (newCareer.length === 0) {
      return;
    }
    // must not create duplicates
    if (
      careersList.find((item) => {
        return item === newCareer;
      })
    ) {
      alert(`"${newCareer}" already exists. Cannot create duplicate careers.`);
      setNewCareer("");
    } else {
      let newList = [...careersList, newCareer];
      setCareersList(newList);
      setCurrentCareerNum(newList.length - 1);
      setNewCareer("");
    }
  }

  return (
    <div>
      <div className="dropdown">
        <button
          type="button"
          className="btn btn-outline-success"
          onClick={() => {
            toggleModal();
          }}
        >
          New Career
        </button>
        <Modal isOpen={modalOn} toggle={toggleModal}>
          <ModalHeader toggle={toggleModal}>New career name:</ModalHeader>
          <ModalBody>
            <div>
              <input
                name="newCareer"
                id="newCareer"
                type="text"
                title="New Career"
                placeholder="career"
                className="form-control"
                onChange={(e) => {
                  setNewCareer(e.target.value);
                }}
                autoFocus
              />
              <button type="button" className="btn btn-success w-100" onClick={addCareer}>
                Add
              </button>
            </div>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={toggleModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    </div>
  );
}
