import React from "react";

export default function Step6({ setTags }) {

  return (
    <div className="step w-50">
      <h3>Add any optional tag words separated by commas.</h3>
      <input
        id="jobTags"
        name="jobTags"
        className="form-control"
        type="string"
        title="Job tags"
        onChange={(e) => {
          setTags(e.target.value);
        }}
      />
    </div>
  );
}
