import { Link } from "react-router-dom";
import { useState } from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

export default function CareerBtnSwitcher({
  careersList,
  currentCareerNum,
  setCurrentCareerNum,
}) {
  const [modalOn, setModalOn] = useState(false);

  function toggleModal() {
    setModalOn(!modalOn);
  }

  return (
    <div>
      <div className="dropdown">
        <button
          type="button"
          className="btn btn-outline-success"
          onClick={() => {
            toggleModal();
          }}
        >
          Switch Career
        </button>
        <Modal isOpen={modalOn} toggle={toggleModal}>
          <ModalHeader toggle={toggleModal}>
            Current career: {careersList[currentCareerNum]}
          </ModalHeader>
          <ModalBody>
            {careersList.map((name, i) => {
              return (
                <div key={i}>
                  <button
                    type="button"
                    className="btn btn-success w-100"
                    disabled={currentCareerNum === i ? true : false}
                    onClick={() => {
                      setCurrentCareerNum(i);
                    }}
                  >
                    {name}
                  </button>
                </div>
              );
            })}
            <div className="text-center">
              <Link to="/about">How to delete a career</Link>
            </div>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={toggleModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    </div>
  );
}
